import React, { useState, useEffect } from 'react'

const GoogleTranslate = ({setuserLangue,userLangue}) => {

    const googleTranslateElementInit = () => {
        new window.google.translate.TranslateElement({
            pageLanguage: 'en', layout:
                window.google.translate.TranslateElement.FloatPosition.TOP_LEFT, 
        },
            'google_translate_element') 
            document.getElementsByClassName('goog-te-combo')[0].addEventListener('input',(e)=>{
                if(setuserLangue){
              setuserLangue(e.target.value)
              }
            })  
            setTimeout(()=>{ 
                if(document.getElementsByClassName('goog-te-combo')[0]){
                var selectElement = document.getElementsByClassName('goog-te-combo')[0];
                var desiredValue = userLangue;
                document.cookie = `googtrans=/en/${desiredValue}`;
                var index = -1;
    
                for (var i = 0; i < selectElement.options.length; i++) {
                if (selectElement.options[i].value === desiredValue) {
                    index = i;
                    break;
                }
                } 
                if(index!=-1){ 
                    document.getElementsByClassName('goog-te-combo')[0].getElementsByTagName('option')[index].selected = 'selected'
                }
                }
            },2000)

    }

    useEffect(() => {
        var addScript = document.createElement('script');
        addScript.setAttribute('src', '//translate.google.com/translate_a/element.js?cb=googleTranslateElementInit');
        document.body.appendChild(addScript);
        window.googleTranslateElementInit = googleTranslateElementInit;
        
    }, []);

    return (
        <>
            <div id="google_translate_element"></div>
            <style jsx>{`
            #goog-gt-tt{
                display:none !important;
                width:0;
                height:0
            }
            `}</style>
        </>
    );

}
export default GoogleTranslate
